.interpretation-container,
.comment-container {
    max-width: 560px;
}[dir] .interpretation-container, [dir] .comment-container {
    background-color: #f6f7f8;
    border-radius: 4px;
    padding: 10px;
    margin-top: 10px;
}

[dir=ltr] .interpretation-container {
    margin-left: 10px;
}

[dir=rtl] .interpretation-container {
    margin-right: 10px;
}

[dir] .comment-container {
    padding: 6px;
}

.interpretation-action-button {
    color: #494949;
    font: inherit;
    font-size: 12px;
    height: 14px;
    line-height: 14px;
    text-decoration: underline;
}

[dir] .interpretation-action-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0 !important;
}

[dir=ltr] .interpretation-action-button {
    margin-right: 10px;
}

[dir=rtl] .interpretation-action-button {
    margin-left: 10px;
}

.interpretation-action-button:focus {
    outline: 0;
}



